import Loading from './Loading'
import Bus from '../assets/eventBus'
import '../../static/css/share/iconfont.css'
import Person from './person.vue'
import LoadingImg from './loadingImg'
  export default {
      name: "bounced",
    props:['userId','type','wishCreate','loveList','changeReason','informReason','wishList','newWish','informList','zanList','isOwer','who','isLoading','total','weixin','imgList','changeColor'],
    data(){
        return{
          api:this.GLOBAL.baseURL,
          marginTop:'-270px',
          height:'500px',
          wishName:'',
          wishIndex:0,
          options:[
            {value:0,label:'共享'},
            {value:1,label:'私密'}
          ],
          imgUrl:'../../static/img/static/back.jpg',
          left:0,
          photoIndex:0,
          viedio:false,
          videoIMg:'',
          statrPlay:1,
          introduce:'',
          websocket:'',
          pageIndex:1,
          totalPage:'',
          replayList:[],
          review:'',
          isadIndex:-1,
          asd: ''
        }
    },
    components:{
      Person,
      Loading,
      LoadingImg
    },
    watch:{
      wishName:function(){
        this.$emit('getName',this.wishName)
      },
    },
    methods:{
      checkUser(val){
        this.$emit('toCheckUser',val)
      },
      toEmail(){
        this.$emit('toEmail')
      },
      sendContent(){
        this.$emit('sendContent',this.introduce)
      },
      lookclick(item,index){

        this.photoIndex = index
      },
      lookVideo(){
      var vide =  document.getElementById('videoI')
        var player = document.getElementById('player')
        vide.play()
        player.style.display = 'none'
        this.getPasue()
      },
      getPasue(){
        var vide =  document.getElementById('videoI')
        var player = document.getElementById('player')
        vide.addEventListener('ended',function(){
          player.style.display = 'block'

        })
      },
      prev(){
        if(this.photoIndex > 3){
          this.photoIndex --
          this.left += 114
        }else if(this.photoIndex >0&& this.left <0){
          this.photoIndex --
          this.left += 114
        }else if(this.photoIndex >0&& this.left >=0){
          this.photoIndex --
        }
      },
      next(){
        if(this.photoIndex < this.imgList.length -1){
          this.photoIndex ++
          if(this.photoIndex > 3){
            this.left -= 114
          }
        }
      },
      changeIndex(val){
        this.photoIndex = '0'
        this.left = 0
       this.$emit('changeIndex',val)
      },
      addWish(){
        this.$emit('addWish')
      },
      addWishName(){
       this.$emit('addWishName')
      },
      getGroup(){

      },
      save(item,index){
        this.$emit('save',item,index)
      },
      close(el,msg){
        this.$emit(el,msg)
      },
      saveWish(){
        this.$emit('saveWish')
      },
      changeForm(){
        this.$emit('changeForm')
      },
      informSend(){
        this.$emit('informSend')
      },
      informAblish(){
        this.$emit('informAblish')
      },
      takeAttention(item,index){
        this.$emit('takeAttention',item,index)
      },
      toPerson(item,index){
        this.$router.push({
          path: '/userdata',
          query: {
            information: item.user_id
          }
        })
      },
      handleCurrentChange(val){
        this.$emit('handleCurrentChange',val)

      },
      toWeixin(){
        this.$emit('toWeixin')
      },
      toWeiBo(){
        this.$emit('toWeiBo')
      },
      atention(item,index){
        this.$http.post(this.api + '/home/Comment/attention',{
          token: localStorage.getItem('token'),
          att_user_id: item.user_id,
          type: 1
        })
          .then(res=>{
            if(res.data.code == 1){
              item.is_mutualatt = 1
              this.$message({
                type: 'success',
                message: '关注成功！'
              })
            }else if(res.data.code == 3){
              this.atention(item,index)
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      reAtention(item,index){
        this.$http.post(this.api + '/home/Comment/attention',{
          token: localStorage.getItem('token'),
          att_user_id: item.user_id,
          type: 2
        })
          .then(res=>{
            if(res.data.code == 1){
              item.is_mutualatt = 0
              this.$message({
                type: 'success',
                message: '取消关注！'
              })
            }else if(res.data.code == 3){
              this.reAtention(item,index)
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      onCopy () {
        this.$message({
          message: `复制成功！`,
          type: 'success'
        });
      },
      onError () {
        this.$message({
          message: `复制失败！`,
          type: 'error'
        });
      }
    },
    created() {
        Bus.$on('returnName',(msg)=>{
          this.wishName = msg
        })
      this.asd = location.href + '&userId='+this.userId
    },
  }



